import Header from "../../components/header";
import React from "react";
import SearchBox from "../../components/common/searchBox";
import { tableData } from '../../constant/table_data';
import Table from "../../components/admin/table";
import { Breadcrumb } from 'antd';


function AdminSearch() {

    const tags = [{ tagName: 123 }, { tagName: '田中' }, { tagName: '2024年2月' }];

    return (
        <>
            {/* <Header page={"adminscreen"} pagetitle={"管理画面 リクエスター管理 ホーム画面"} /> */}
            <section id="main-section">
                <div className="py-12 bg-header">
                    <Breadcrumb
                        className="p-5"
                        separator=">"
                        items={[
                            {
                                title: 'ADMINISTER_SCREEN',
                                href: 'designer'
                            },
                            {
                                title: 'DESIGHNER_MANAGEMENT_SCREEN',
                            },
                        ]}
                    />
                    <SearchBox title="REQUEST SEARCH" tags={tags} />
                </div>
                <Table tableData={tableData} />

            </section>
        </>
    );
}

export default AdminSearch;
